import React from 'react';
import { Card, CardImg, CardBody, CardText, CardTitle, Breadcrumb, BreadcrumbItem } from 'reactstrap';
import { Link } from 'react-router-dom';

function RenderDish({dish}) {
    
    return (
        <Card>
            <CardImg width="100%" src={dish.image} alt={dish.name} />
            <CardBody>
                <CardTitle> {dish.name} </CardTitle>
                <CardText>
                    {dish.description}
                </CardText>
            </CardBody>    
        </Card>

    );
}

const RenderComments = (props) => {
    
    const DishComments = props.comments.map((individualcomment) => {
        return (
            <div key={individualcomment.id}>
                <br />
                <p>
                    {individualcomment.comment}
                    <br />
                    -- {individualcomment.author} , {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(individualcomment.date)))}
                </p>
            </div>
        );
    });

    return (
        <div className='col-12 col-md-5 m-1'>
            <h4>
                Comments
            </h4>
            {DishComments}
        </div>
    );

}

        

const DishDetail = (props) => {
    
    const dish = props.dish;

    if(dish!=null) 
    {
    
        return(
            <div className='container'>

                <div className="row">
                    <div className="col-12">    
                        <Breadcrumb>
                            <BreadcrumbItem><Link to='/menu'> Menu </Link></BreadcrumbItem>
                            <BreadcrumbItem active> {props.dish.name}  </BreadcrumbItem>
                        </Breadcrumb>
                    </div>
                    <div className="col-12">
                        <h3> {props.dish.name} </h3>
                        <hr />
                    </div>
                </div>

                <div className="row">
                    <div className="col-12 col-md-5 m-1">
                        <RenderDish dish = {props.dish} />
                    </div>
                    <RenderComments comments = {props.comments} />
                </div>
            </div>             
        )
    }
    else {
        return(
            <div></div>
        )
    }
}

export default DishDetail;